<template>
  <div></div>
</template>

<script>
const wx = require('weixin-js-sdk');
import axios from 'axios';

export default {
  name: 'QrScan', // 
  data () {
    return {
      loading: true,
      signData: null, // 
    }
  },
  methods: {
    async scanQRCode () {
      // const url = encodeURIComponent(location.href.split('#')[0]);
      const _this = this,
            url = location.href.split('#')[0];

      let signData = this.signData;

      if (!signData || !signData.signature) {
        // let res = await axios.get('/api/v1/wx/jsConfig?url=' + url);
        let res = await axios.post('http://wechat-test.humandetail.com/getSignature', {
          url
        });
        res = res.data;
        // alert(JSON.stringify(res));
        // if (res.code !== 0) {
        //   alert('');
        //   return false;
        // }

        signData = res.data;
      }

      const {
        signature,
        noncestr,
        appid,
        timestamp
      } = signData;
      const config = {
        debug: true, // ,apialertpclogpc
        appId: appid, // 
        timestamp: timestamp, // 
        nonceStr: noncestr, // 
        signature: signature,// 
        jsApiList: ['scanQRCode'] // JS
      }
      // alert(JSON.stringify(config));
      wx.config(config);
      wx.ready(() => {
        wx.scanQRCode({
          needResult: 1, // 01
          scanType: ["qrCode","barCode"], // 
          success: _this.onScanSuccess
        });
      });
      wx.error((err) => {
        console.log(err)
      })
    },

    onScanSuccess (res) {
      let result = res.resultStr; // needResult  1 
      this.result = result;

      // 
      const redirectUrl = `${result}${result.indexOf('?') > -1 ? '&' : '?'}from=${encodeURIComponent(window.location.href)}&action=publicScan&r=${Math.random()}`;

      this.redirectUrl = redirectUrl;

      setTimeout(() => {
        this.redirect(redirectUrl);
      }, 2000)
      alert(result);
    },

    redirect (url) {
      window.location.href = url;
      // const oA = document.createElement('a');
      // oA.href = url;

      // document.body.appendChild(oA);
      // oA.click();
      // oA.remove();
    }
  },
  activated () {
    this.scanQRCode();
  }
}
</script>